import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col,Divider } from 'antd';
import TableDropoutList from './TableDropoutList';
import SearchBar from './SearchBar';
import ContinueLearning from './ContinueLearning';
import './style.scss';

class DropoutListlPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Danh sách thôi, ngừng học">
        <SearchBar />
        <Row>
          <Col span={24}>
            <TableDropoutList />
          </Col>
          <Divider>Danh sách SV có quyết định học tiếp</Divider>
          <Col span={24}>
            <ContinueLearning />
          </Col>
          
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DropoutListlPage />
  </Page>
);



