import { request } from 'utils';

const prefix = '/api/students';

export const getStudentInAcademicNoteForContinue = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  notContinue,
}) =>
  request({
    prefix,
    url: '/AcademicNote/GetStudentInAcademicNoteForContinue',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      notContinue,
    },
    data: {},
  });

export const createAcademicNoteAndANDetailForContinue = dataCreate =>
  request({
    prefix,
    url: '/AcademicNote/CreateAcademicNoteAndANDetailForContinue',
    method: 'POST',
    data: { ...dataCreate },
  });

