import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { SelectAjax } from 'components/common';

import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { getApprovalTypeForContinue } from 'services/ApprovalType';

@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const { getFieldDecorator, setFieldsValue } = this.props.form;
    // const listParamSearch = this.state.paramSearch;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };

    return (
      <Form
        className="form-inline-label scholarship-filter"
        layout="vertical"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            span={5}
          />

          <Col span={7}>
            <Form.Item label="Mã sinh viên">
              {getFieldDecorator('system11', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={7}>
            <Form.Item label="Họ tên">{getFieldDecorator('system12', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={7}>
            <Form.Item label="Loại quyết định">
              {getFieldDecorator(
                'system13',
                {}
              )(
                <SelectAjax
                  service={getApprovalTypeForContinue}
                  setFieldsValue={setFieldsValue}
                  params={{}}
                  keyValue="id"
                />
                // <Select>
                //   <Option value="1">Học tiếp</Option>
                //   <Option value="2">Thôi học</Option>
                // </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



