import React, { Component } from 'react';
import { BtnAddNew } from 'components/common/button';
import { TableData } from 'components/common';
import moment from 'moment';
import { getQueryParams } from 'utils/urlUtil';
import { getStudentInAcademicNoteForContinue } from 'services/AcademicNote';
import ModalAddStudent from './ModalAddNewStudent';

const columns = [
  {
    title: 'Số QĐ',
    dataIndex: 'approvalNumber',
    key: 'approvalNumber',
  },
  {
    title: 'Ngày QĐ',
    dataIndex: 'approvalDate',
    key: 'approvalDate',
    render: (_text, record) => {
      return <span>{moment(record.approvalDate).format('DD-MM-YYYY')}</span>;
    },
  },
  {
    title: 'Nội dung',
    dataIndex: 'reason',
    key: 'reason',
  },
  {
    title: 'Mã SV',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'className',
    key: 'className',
  },
];

class TableDropoutList extends Component {
  state = { paramSearchClass: {}, studentsData: {}, showModal: false, selectedRowKeys: [] };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  render() {
    const { paramSearchClass, studentsData, selectedRowKeys, showModal } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: (selectedKeys, selectedRows) => {
        const students = {};
        selectedRows.forEach(item => {
          students[item.studentCode] = item;
        });

        this.setState({ studentsData: students, selectedRowKeys: selectedKeys });
      },
    };
    return (
      <div>
        <div className="btn-group">
          <BtnAddNew
            disabled={Object.values(studentsData).length === 0}
            title="Nhập xét học tiếp"
            onClick={() => {
              this.setState({ showModal: true });
            }}
          />
        </div>
        <TableData
          rowSelection={rowSelection}
          service={getStudentInAcademicNoteForContinue}
          paramSearch={{ ...paramSearchClass, notContinue: true }}
          columns={columns}
          allowGetData={!!paramSearchClass.courseId}
          rowKey="studentCode"
        />
        <ModalAddStudent
          showModal={showModal}
          students={studentsData}
          hideModal={() => this.setState({ showModal: false, selectedRowKeys: [] })}
        />
      </div>
    );
  }
}

export default TableDropoutList;



