import { request } from 'utils';

const prefix = '/api/students';

export const getApprovalTypeForContinue = () =>
  request({
    prefix,
    url: '/ApprovalType/GetApprovalTypeForContinue',
    method: 'GET',
    params: {},
    data: {},
  });

