import React, { Component } from 'react';
// import { BtnAddNew } from 'components/common/button';
import { TableData } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { getStudentInAcademicNoteForContinue } from 'services/AcademicNote';
import moment from 'moment';

const columns = [
  {
    title: 'Số QĐ',
    dataIndex: 'approvalNumber',
    key: 'approvalNumber',
  },
  {
    title: 'Ngày QĐ',
    dataIndex: 'approvalDate',
    key: 'approvalDate',
    render: (_text, record) => {
      return <span>{moment(record.approvalDate).format('DD-MM-YYYY')}</span>;
    },
  },
  {
    title: 'Nội dung',
    dataIndex: 'reason',
    key: 'reason',
  },
  {
    title: 'Mã SV',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'className',
    key: 'className',
  },
];

class ContinueLearning extends Component {
  state = { paramSearchClass: {} };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  render() {
    const { paramSearchClass } = this.state;
    return (
      <div>
        <TableData
          service={getStudentInAcademicNoteForContinue}
          paramSearch={{ ...paramSearchClass, notContinue: false }}
          columns={columns}
          allowGetData={!!paramSearchClass.courseId}
          rowKey="id"
        />
      </div>
    );
  }
}

export default ContinueLearning;



