import React, { useState, useEffect } from 'react';
import { Row, Spin, Card, Form, Input, Table, Select, message } from 'antd';
import { SelectAjax, DatePickerString } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getCourses } from 'services/course';
import { createAcademicNoteAndANDetailForContinue } from 'services/AcademicNote';
import ModalHook from 'components/common/ModalHook';
import to from 'await-to-js';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';
import { getClasss } from 'services/class';
// import TableForFormEntering from './TableForFormEntering';

export const MODAL_ID = 'ModalAddListStudentDropout';

const ModalAddNewStudent = props => {
  const paramsSearch = getQueryParams();
  // const [studentData, setStudentData] = useState({});
  const [classData, setClassData] = useState([]);

  const [addStudent, setAddStudent] = useState({});
  const [loading, setLoading] = useState(false);
  const { getFieldDecorator, validateFields, resetFields, setFieldsValue } = props.form;

  const handleCancel = () => {
    props.hideModal();
    resetFields();
  };
  const handleOk = async () => {
    validateFields(async (error, values) => {
      if (error) return;
      setLoading(true);
      const dataStudentAdd = Object.values(addStudent).map(item => {
        return {
          studentId: item.studentId,
          courseId: values.courseId,
          classContinuteId: item.classContinuteId,
          semesterId: values.semesterId,
          yearEduId: values.yearEduId,
        };
      });
      if (dataStudentAdd.length === 0) {
        message.error('Chưa chọn lớp');
        setLoading(false);
        return;
      }
      const dataCreate = {
        approvalNumber: values.approvalNumber,
        approvalDate: values.approvalDate,
        approvalTypeId: values.approvalTypeId,
        description: values.description,
        anDetail: dataStudentAdd,
      };

      const [err] = await to(createAcademicNoteAndANDetailForContinue(dataCreate));
      setLoading(false);
      if (err) return;
      message.success('Lưu thành công');
      const { _time } = paramsSearch;
      const countTine = _time + 1;
      updateQueryParams({ ...paramsSearch, _time: countTine });
      handleCancel();
    });
    return false;
  };

  const handleChangeClass = (record, value) => {
    const dataStudent = {};
    dataStudent[record.studentId] = {
      studentId: record.studentId,
      classContinuteId: value,
    };
    setAddStudent({ ...addStudent, ...dataStudent });
  };

  const columns = [
    {
      title: 'Mã sinh viên',
      dataIndex: 'studentCode',
      key: 'studentCode',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
    },
    {
      title: 'Tên lớp',
      dataIndex: 'className',
      key: 'className',
      render: (text, record) => {
        const dataClass = classData.filter(item => item.id !== record.classId);
        return (
          <Form.Item>
            {getFieldDecorator(`class-${record.studentId}`, { rules: [requiredRule] })(
              <Select style={{ width: 200 }} onChange={value => handleChangeClass(record, value)}>
                {dataClass.map(item => (
                  <Option value={item.id}>{item.label}</Option>
                ))}
              </Select>
            )}
          </Form.Item>
        );
      },
    },
  ];

  const getDataClass = async () => {
    const [err, response] = await to(getClasss());
    if (err) return;
    setClassData(response.data.items);
  };

  useEffect(() => {
    // setStudentData(props.students);
  }, [props.students]);

  useEffect(() => {
    getDataClass();
  }, []);

  const { Option } = Select;
  const { TextArea } = Input;
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
    colon: false,
  };

  return (
    <ModalHook
      id={MODAL_ID}
      okText="Lưu"
      width="800px"
      title="Nhập sinh viên"
      visible={props.showModal}
      onOk={handleOk}
      onCancel={handleCancel}
    >
      <Spin spinning={loading}>
        <Form className="form-inline-label" layout="vertical">
          <Card title="Nhập Quyết định thôi học, ngừng học">
            <Row>
              <Form {...formItemLayout}>
                <Form.Item {...formItemLayout} label="Năm học" required>
                  {getFieldDecorator('yearEduId', { rules: [requiredRule] })(
                    <SelectAjax
                      service={getYearEducations}
                      setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Học kì" required>
                  {getFieldDecorator('semesterId', { rules: [requiredRule] })(
                    <SelectAjax
                      setFieldsValue={setFieldsValue}
                      service={getSemesters}
                      // allowGetData={!!yearEduId}
                      // params={{ yearEduId }}
                      typeValue="int"
                    />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Khoá học">
                  {getFieldDecorator('courseId', { rules: [requiredRule] })(
                    <SelectAjax setFieldsValue={setFieldsValue} service={getCourses} />
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Số QĐ">
                  {getFieldDecorator('approvalNumber', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Ngày QĐ">
                  {getFieldDecorator('approvalDate', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<DatePickerString placeholder="Chọn ngày" />)}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Loại QĐ">
                  {getFieldDecorator('approvalTypeId', {
                    initialValue: 6,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(
                    <Select>
                      <Option value={6}>Học tiếp</Option>
                    </Select>
                  )}
                </Form.Item>
                <Form.Item {...formItemLayout} label="Nội dung">
                  {getFieldDecorator('description', {
                    // rules: [
                    //   {
                    //     required: true,
                    //     message: 'Vui lòng nhập thông tin!',
                    //   },
                    // ],
                  })(<TextArea style={{ height: 100 }} />)}
                </Form.Item>
              </Form>
            </Row>
          </Card>
        </Form>
        <Row>
          <Table
            columns={columns}
            rowKey="studentCode"
            dataSource={Object.values(props.students)}
          />
        </Row>
      </Spin>
    </ModalHook>
  );
};

export default Form.create()(ModalAddNewStudent);



